* {
  font-family: 'Roboto', sans-serif;
}

body {
  background-color: #f2f2f2;
}

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  background-color: #f2f2f2;
}

.gif-container {
  display: flex;
  flex-flow: row wrap;
  margin-left: -15px; 
  width: 60%;
}

.gif-container img {
  flex: auto;
  height: 150px;
  min-width: 150px;
  margin: 0 15px 15px 0; 
  box-shadow: 7px 10px 24px -6px rgba(0,0,0,0.75);
  cursor: pointer;
}

img[src=""] {
    display: none;
}

form {
  background-color: #f2f2f2;
  text-align: center;
  padding: 30px;
}

form input {
  outline: none;
  padding: 10px;
  border: 2px solid black;
  width: 200px;
}

#btn {
  display: none;
}

.fa {
  position: fixed;
  opacity: 0;
  top: 660px;
  right: 10px;
  font-size: 40px;
  cursor: pointer;
  transition: all 300ms ease;
}

.overlay {
  position: fixed; /* Sit on top of the page content */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.9); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
}

.modal {
  display: flex;
  justify-content: center;
  width: 100%;
}

.modalImg {
  position: fixed;
  z-index: 3;
  cursor: pointer;
  width: 40%;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.message {
  display: flex;
  justify-content: center;
  width: 100%;
}




